<template>
  <div id="app">
    <div class="spin-container" v-show="loading">
      <Spin fix></Spin>
    </div>
    <transition name="fadeIn" enter-active-class="animated fadeIn" leave-active-class="animated fadeIn" :duration="200">
      <router-view></router-view>
    </transition>
  </div>
</template>

<script>
    export default {
        name: 'App',
        data(){
            return{
                loading:true,
            }
        },
        created(){
            let that=this;
            setTimeout(function(){
                that.loading=false;
            },1000)
        },
        methods:{

        }
    }
</script>

<style lang="less">
  * {
    margin: 0;
    padding: 0;
    font-size: 14px;
    box-sizing: border-box;
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
  }

  html, body {
    width: 100%;
    background: #efefef;
  }

  #app {
    position: relative;
    max-width: 480px;
    margin: auto;
    background: #1E1E1E;
    color: #ffffff;
    .ivu-spin-fix{
      background: rgba(255,255,255,.4);
    }
  }
  .spin-container{
    display: inline-block;
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 99;
    border: 1px solid #eee;
  }
</style>



